<template>
  <v-data-table
    :headers="headers"
    :items="playerStatisticsForDisplay"
    disable-pagination
    hide-default-footer
    must-sort
    sort-by="name"
  >
    <template v-slot:item.box0="{ item }">
      <v-chip
        v-if="displayChip(item, 0)"
        :color="getColor(item, 0)"
      >{{ item.boxWins[0] }}/{{ item.box[0] }}</v-chip>
      <span v-if="!displayChip(item, 0)">{{ item.boxWins[0] }}/{{ item.box[0] }}</span>
    </template>
