      >{{ item.boxWins[1] }}/{{ item.box[1] }}</v-chip>
      <span v-if="!displayChip(item, 1)">{{ item.boxWins[1] }}/{{ item.box[1] }}</span>
    </template>
    <template v-slot:item.box2="{ item }">
      <v-chip
        v-if="displayChip(item, 2)"
        :color="getColor(item, 2)"
      >{{ item.boxWins[2] }}/{{ item.box[2] }}</v-chip>
      <span v-if="!displayChip(item, 2)">{{ item.boxWins[2] }}/{{ item.box[2] }}</span>
    </template>
    <template v-slot:item.box3="{ item }">
      <v-chip
        v-if="displayChip(item, 3)"
        :color="getColor(item, 3)"
      >{{ item.boxWins[3] }}/{{ item.box[3] }}</v-chip>
      <span v-if="!displayChip(item, 3)">{{ item.boxWins[3] }}/{{ item.box[3] }}</span>
    </template>
    <template v-slot:item.box4="{ item }">
      <v-chip
        v-if="displayChip(item, 4)"
        :color="getColor(item, 4)"
      >{{ item.boxWins[4] }}/{{ item.box[4] }}</v-chip>
      <span v-if="!displayChip(item, 4)">{{ item.boxWins[4] }}/{{ item.box[4] }}</span>
    </template>
    <template v-slot:item.box5="{ item }">
      <v-chip
        v-if="displayChip(item, 5)"
        :color="getColor(item, 5)"
      >{{ item.boxWins[5] }}/{{ item.box[5] }}</v-chip>
      <span v-if="!displayChip(item, 5)">{{ item.boxWins[5] }}/{{ item.box[5] }}</span>
    </template>
    <template v-slot:item.box6="{ item }">
      <v-chip
        v-if="displayChip(item, 6)"
        :color="getColor(item, 6)"
      >{{ item.boxWins[6] }}/{{ item.box[6] }}</v-chip>
      <span v-if="!displayChip(item, 6)">{{ item.boxWins[6] }}/{{ item.box[6] }}</span>
    </template>
    <template v-slot:item.box7="{ item }">
      <v-chip
        v-if="displayChip(item, 7)"
        :color="getColor(item, 7)"
      >{{ item.boxWins[7] }}/{{ item.box[7] }}</v-chip>
      <span v-if="!displayChip(item, 7)">{{ item.boxWins[7] }}/{{ item.box[7] }}</span>
    </template>
    <template v-slot:item.box8="{ item }">
      <v-chip
        v-if="displayChip(item, 8)"
        :color="getColor(item, 8)"
      >{{ item.boxWins[8] }}/{{ item.box[8] }}</v-chip>
      <span v-if="!displayChip(item, 8)">{{ item.boxWins[8] }}/{{ item.box[8] }}</span>
    </template>
    <template v-slot:item.box9="{ item }">
      <v-chip
        v-if="displayChip(item, 9)"
        :color="getColor(item, 9)"
      >{{ item.boxWins[9] }}/{{ item.box[9] }}</v-chip>
      <span v-if="!displayChip(item, 9)">{{ item.boxWins[9] }}/{{ item.box[9] }}</span>
    </template>
  </v-data-table>
</template>

<script>
export default {
  name: "BoxStatistic",
  props: { playerStatisticsForDisplay: Array, players: Array },
  data: () => ({
    items: [],
    options: {},
    headers: [
      { text: "Игрок", value: "name" },
      { text: "1 номер", value: "box0", sortable: false },
      { text: "2 номер", value: "box1", sortable: false },
      { text: "3 номер", value: "box2", sortable: false },
      { text: "4 номер", value: "box3", sortable: false },
      { text: "5 номер", value: "box4", sortable: false },
      { text: "6 номер", value: "box5", sortable: false },
      { text: "7 номер", value: "box6", sortable: false },
      { text: "8 номер", value: "box7", sortable: false },
      { text: "9 номер", value: "box8", sortable: false },
      { text: "10 номер", value: "box9", sortable: false }
    ]
  }),
  methods: {
    displayChip(item, boxIndex) {
      var highligting = this.getHighligting(item, boxIndex);
      return highligting != "none";
    },
    getColor(item, boxIndex) {
      return this.getHighligting(item, boxIndex);
    },
    getHighligting(item, boxIndex) {
      if (item.box[boxIndex] == 0) {
        return "none";
      }

      if (item.boxWins[boxIndex] == item.box[boxIndex]) {
        return "rgb(0, 255, 0)";
      } else if ((item.boxWins[boxIndex] * 100) / item.box[boxIndex] > 60) {
        return "rgba(0, 255, 0, 0.5)";
      } else if (item.boxWins[boxIndex] == 0) {
        return "rgb(255, 0, 0)";
      }
      return "none";
    }
  }
};
</script>