<template>
  <md-tabs v-if="database">
    <md-tab id="tab-base" md-label="Основная статистика">
      <div>
        <v-data-table
          :headers="headers"
          :items="playerStatisticsForDisplay"
          disable-pagination
          hide-default-footer
          multi-sort
          mobile-breakpoint="0"
          :sort-by="['isRating', 'relativePoints', 'absolutePoints', 'games', 'name']"
          :sort-desc="[true, true, true, true, false]"
        >
          <template v-slot:item="{ item }">
            <tr v-bind:class="{ notRating: !item.isRating}">
              <td>{{item.name}}</td>
              <td>{{item.relativePoints || 0}}</td>
              <td>{{item.games}}</td>
              <td>{{item.wins}}</td>
              <td>{{item.firstKilled}}</td>
              <td>{{item.firstKilledGuessPoints}}</td>
              <td>{{item.sherif}}</td>
              <td>{{item.sherifWins}}</td>
              <td>{{item.citizen}}</td>
              <td>{{item.don}}</td>
              <td>{{item.donWins}}</td>
              <td>{{item.maf}}</td>
              <td>{{item.mafWins}}</td>
              <td>{{item.absolutePoints}}</td>
            </tr>
          </template>
        </v-data-table>
      </div>
      <div class="games-statistic games-statistic-all">Всего игр: {{gamesCount}}</div>
      <div class="games-statistic games-statistic-red-wins">Побед мирных: {{redWinsCount}}</div>
      <div class="games-statistic games-statistic-black-wins">Побед мафии: {{blackWinsCount}}</div>
      <div
        class="games-statistic games-statistic-rating"
      >Игр для попадания в рейтинг: {{gamesForRatingCount}} (12%)</div>
    </md-tab>
    <md-tab id="tab-roles" md-label="По ролям">
      <RoleStatistic
        :playerStatisticsForDisplay="playerStatisticsForDisplayRating"
        :players="ratingPlayers"
      />
    </md-tab>
    <md-tab id="tab-players" md-label="По игрокам">
      <PlayerStatistic
        :playerStatisticsForDisplay="playerStatisticsForDisplayRating"
        :players="ratingPlayers"
      />
    </md-tab>
    <md-tab id="tab-box" md-label="По номерам">
      <BoxStatistic
        :playerStatisticsForDisplay="playerStatisticsForDisplayRating"
        :players="ratingPlayers"
      />
    </md-tab>
  </md-tabs>
</template>

<script>
import RoleStatistic from "./RoleStatistic.vue";
import PlayerStatistic from "./PlayerStatistic.vue";
import BoxStatistic from "./BoxStatistic.vue";

import json from "../json/games.json";

export default {
  name: "Statistics",
  components: {
    RoleStatistic,
    PlayerStatistic,
    BoxStatistic
  },
  props: {},
  data: () => ({
    gamesCount: 0,
    redWinsCount: 0,
    blackWinsCount: 0,
    gamesForRatingCount: 0,
    ratingThreshold: 0.12,
    currentSort: "name",
    currentSortOrder: "asc",
    database: null,
    players: null,
    ratingPlayers: null,
    games: null,
    playerStatistics: {},
    playerStatisticsForDisplay: [],
    playerStatisticsForDisplayRating: [],
    headers: [
      { text: "Игрок", value: "name", sortable: false },
      { text: "Очки", value: "relativePoints", sortable: false },
      { text: "Игр", value: "games", sortable: false },
      { text: "Побед", value: "wins", sortable: false },
      { text: "Первый убитый", value: "firstKilled", sortable: false },
      { text: "Лучший ход", value: "firstKilledGuessPoints", sortable: false },
      { text: "Игр шерифом", value: "sherif", sortable: false },
      { text: "Побед шерифом", value: "sherifWins", sortable: false },
      { text: "Игр мирным", value: "citizen", sortable: false },
      { text: "Игр доном", value: "don", sortable: false },
      { text: "Побед доном", value: "donWins", sortable: false },
      { text: "Игр мафом", value: "maf", sortable: false },
      { text: "Побед мафом", value: "mafWins", sortable: false },
      { text: "Баллы", value: "absolutePoints", sortable: false },
    ]
  }),
  async mounted() {
    if (process.env.NODE_ENV !== "development") {
      var currentYearAndMonth = new Date()
        .toISOString()
        .substr(0, 7)
        .split("-");
      var year = currentYearAndMonth[0];
      var month = currentYearAndMonth[1];

      for (;;) {
        //with leading zeroes for monthes 1-9
        var stringMonth = ("0" + month).slice(-2);

        var url = process.env.VUE_APP_DATA_URL.replace(
          "{month}",
          year + "-" + stringMonth
        );

        var response = await fetch(url);

        if (response.ok) {
          var data = await response.json();
          this.database = data;
          this.players = data.players;
          this.games = data.games;

          this.calculateStatistic();
          break;
        }

        //calculate previous month
        month--;
        if (month == 0) {
          month = 12;
          year--;
          if (year == 2018) {
            alert("Игры не найдены");
          }
        }
      }
    } else {
      this.database = json;
      this.players = json.players;
      this.games = json.games;

      this.calculateStatistic();
    }
  },
  methods: {
    customSort(value) {
      return value.sort((a, b) => {
        const sortBy = this.currentSort;

        if (this.currentSortOrder === "desc") {
          if (a[sortBy] > b[sortBy]) {
            return -1;
          }
          if (a[sortBy] < b[sortBy]) {
            return 1;
          }
          return 0;
        }

        if (a[sortBy] > b[sortBy]) {
          return 1;
        }
        if (a[sortBy] < b[sortBy]) {
          return -1;
        }
        return 0;
      });
    },
    getPlayerName(id) {
      return this.players.find(x => x.id === id).name;
    },
    calculateStatistic() {
      var self = this;
      this.games.forEach(function(game) {
        var gamePlayersStatistics = [];
        game.players.forEach(function(playerId) {
          gamePlayersStatistics.push(self.getPlayerStatisticById(playerId));
        });

        for (var i = 0; i < 10; i++) {
          var gamePlayersStatistic = gamePlayersStatistics[i];

          gamePlayersStatistic.games++;
          gamePlayersStatistic.box[i]++;
          if (self.playerWinGame(game, i)) {
            gamePlayersStatistic.wins++;
            gamePlayersStatistic.boxWins[i]++;
          }
          if (self.playerIsSherif(game, i)) {
            gamePlayersStatistic.sherif++;
          }
          if (self.playerIsSherifAndWin(game, i)) {
            gamePlayersStatistic.sherifWins++;
          }
          if (self.playerIsCitizen(game, i)) {
            gamePlayersStatistic.citizen++;
          }
          if (self.playerIsCitizenAndWin(game, i)) {
            gamePlayersStatistic.citizenWins++;
          }
          if (self.playerIsDon(game, i)) {
            gamePlayersStatistic.don++;
          }
          if (self.playerIsDonAndWin(game, i)) {
            gamePlayersStatistic.donWins++;
          }
          if (self.playerIsMaf(game, i)) {
            gamePlayersStatistic.maf++;
          }
          if (self.playerIsMafAndWin(game, i)) {
            gamePlayersStatistic.mafWins++;
          }
          if (self.firstKilled(game, i)) {
            gamePlayersStatistic.firstKilled++;
            gamePlayersStatistic.firstKilledGuessPoints +=
              game.firstKilledGuessPoints;
          }

          for (var j = 0; j < 10; j++) {
            if (i === j) {
              continue;
            }

            var statisticWithPlayer = self.getStatisticWithOtherPlayerById(
              gamePlayersStatistic,
              game.players[j]
            );

            statisticWithPlayer.gamesTogether++;

            if (self.playerIsRed(game, i) && self.playerIsRed(game, j)) {
              statisticWithPlayer.bothRed++;
              if (game.cityWin) {
                statisticWithPlayer.bothRedWins++;
              }
            }

            if (self.playerIsBlack(game, i) && self.playerIsBlack(game, j)) {
              statisticWithPlayer.bothBlack++;
              if (!game.cityWin) {
                statisticWithPlayer.bothBlackWins++;
              }
            }

            if (self.playerIsRed(game, i) && self.playerIsBlack(game, j)) {
              statisticWithPlayer.playerRed++;
              if (game.cityWin) {
                statisticWithPlayer.playerRedWins++;
              }
            }

            if (self.playerIsBlack(game, i) && self.playerIsRed(game, j)) {
              statisticWithPlayer.playerBlack++;
              if (!game.cityWin) {
                statisticWithPlayer.playerBlackWins++;
              }
            }
          }
        }
      });

      this.gamesForRatingCount = Math.ceil(
        this.games.length * this.ratingThreshold
      );
      this.ratingPlayers = [];

      //set statistic for display
      this.players.forEach(function(player) {
        var playerStatistic = self.getPlayerStatisticById(player.id);

        playerStatistic.isRating =
          playerStatistic.games >= self.gamesForRatingCount;

        self.calculatePoints(playerStatistic);

        //calculate win percents
        playerStatistic.sherifWinPercent = self.calculatePercent(
          playerStatistic.sherifWins,
          playerStatistic.sherif
        );
        playerStatistic.citizenWinPercent = self.calculatePercent(
          playerStatistic.citizenWins,
          playerStatistic.citizen
        );
        playerStatistic.donWinPercent = self.calculatePercent(
          playerStatistic.donWins,
          playerStatistic.don
        );
        playerStatistic.mafWinPercent = self.calculatePercent(
          playerStatistic.mafWins,
          playerStatistic.maf
        );

        self.playerStatisticsForDisplay.push(playerStatistic);

        if (playerStatistic.isRating) {
          self.ratingPlayers.push(player);
          self.playerStatisticsForDisplayRating.push(playerStatistic);
        }
      });

      //calculate statistics with other players
      this.players.forEach(function(player) {
        var playerStatistic = self.getPlayerStatisticById(player.id);

        playerStatistic.withOtherPlayers = self.calculateStatisticWithOtherPlayers(
          playerStatistic
        );
      });

      //sort players
      this.ratingPlayers.sort(this.sortByNameFunction);

      //calculate all games statistic
      this.gamesCount = this.games.length;
      this.redWinsCount = this.games.filter(game => game.cityWin).length;
      this.blackWinsCount = this.games.filter(game => !game.cityWin).length;
    },
    calculateStatisticWithOtherPlayers(playerStatistic) {
      var result = [];
      var self = this;
      this.players.forEach(function(otherPlayer) {
        if (otherPlayer.id == playerStatistic.id) {
          return;
        }
        var withOtherPlayer = playerStatistic.withOtherPlayers[otherPlayer.id];

        if (!withOtherPlayer) {
          return;
        }

        var otherPlayerStatistic = self.getPlayerStatisticById(otherPlayer.id);
        if (!otherPlayerStatistic.isRating) {
          return;
        }

        withOtherPlayer.bothRedWinPercent = self.calculatePercent(
          withOtherPlayer.bothRedWins,
          withOtherPlayer.bothRed
        );
        withOtherPlayer.bothBlackWinPercent = self.calculatePercent(
          withOtherPlayer.bothBlackWins,
          withOtherPlayer.bothBlack
        );
        withOtherPlayer.playerRedWinPercent = self.calculatePercent(
          withOtherPlayer.playerRedWins,
          withOtherPlayer.playerRed
        );
        withOtherPlayer.playerBlackWinPercent = self.calculatePercent(
          withOtherPlayer.playerBlackWins,
          withOtherPlayer.playerBlack
        );
        result.push(withOtherPlayer);
      });
      return result;
    },
    getPlayerStatisticById(id) {
      var result = this.playerStatistics[id];
      if (!result) {
        result = {
          id: id,
          name: this.getPlayerName(id),
          games: 0,
          wins: 0,
          sherif: 0,
          sherifWins: 0,
          sherifWinPercent: 0,
          citizen: 0,
          citizenWins: 0,
          citizenWinPercent: 0,
          don: 0,
          donWins: 0,
          donWinPercent: 0,
          maf: 0,
          mafWins: 0,
          mafWinPercent: 0,
          firstKilled: 0,
          firstKilledGuessPoints: 0,
          absolutePoints: 0,
          relativePoints: 0,
          box: Array(10).fill(0),
          boxWins: Array(10).fill(0),
          isRating: false,
          withOtherPlayers: {}
        };
        this.playerStatistics[id] = result;
      }
      return result;
    },
    getStatisticWithOtherPlayerById(playerStatistic, otherPlayerId) {
      var result = playerStatistic.withOtherPlayers[otherPlayerId];
      if (!result) {
        result = {
          id: otherPlayerId,
          name: this.getPlayerName(otherPlayerId),
          gamesTogether: 0,
          bothRed: 0,
          bothRedWins: 0,
          bothRedWinPercent: 0,
          bothBlack: 0,
          bothBlackWins: 0,
          bothBlackWinPercent: 0,
          playerRed: 0,
          playerRedWins: 0,
          playerRedWinPercent: 0,
          playerBlack: 0,
          playerBlackWins: 0,
          playerBlackWinPercent: 0
        };
        playerStatistic.withOtherPlayers[otherPlayerId] = result;
      }
      return result;
    },
    playerWinGame(game, playerIndex) {
      return game.don == playerIndex ||
        game.maf1 == playerIndex ||
        game.maf2 == playerIndex
        ? !game.cityWin
        : game.cityWin;
    },
    playerIsSherif(game, playerIndex) {
      return game.sherif == playerIndex;
    },
    playerIsSherifAndWin(game, playerIndex) {
      return game.cityWin && this.playerIsSherif(game, playerIndex);
    },
    playerIsCitizen(game, playerIndex) {
      return !(
        game.don == playerIndex ||
        game.maf1 == playerIndex ||
        game.maf2 == playerIndex ||
        game.sherif == playerIndex
      );
    },
    playerIsCitizenAndWin(game, playerIndex) {
      return game.cityWin && this.playerIsCitizen(game, playerIndex);
    },
    playerIsDon(game, playerIndex) {
      return game.don == playerIndex;
    },
    playerIsDonAndWin(game, playerIndex) {
      return !game.cityWin && this.playerIsDon(game, playerIndex);
    },
    playerIsMaf(game, playerIndex) {
      return game.maf1 == playerIndex || game.maf2 == playerIndex;
    },
    playerIsMafAndWin(game, playerIndex) {
      return !game.cityWin && this.playerIsMaf(game, playerIndex);
    },
    firstKilled(game, playerIndex) {
      return game.firstKilled == playerIndex;
    },
    calculatePoints(playerStatistic) {
      playerStatistic.absolutePoints =
        playerStatistic.wins + playerStatistic.firstKilledGuessPoints;
      playerStatistic.relativePoints = Math.round(
        (playerStatistic.absolutePoints * 100) / playerStatistic.games
      );
    },
    playerIsBlack(game, playerIndex) {
      return (
        game.don == playerIndex ||
        game.maf1 == playerIndex ||
        game.maf2 == playerIndex
      );
    },
    playerIsRed(game, playerIndex) {
      return !this.playerIsBlack(game, playerIndex);
    },
    calculatePercent(value, fromValue) {
      if (fromValue == 0) {
        return 0;
      }
      return Math.round((value * 1000) / fromValue) / 10;
    },
    sortByNameFunction(a, b) {
      if (a.name > b.name) {
        return 1;
      }
      if (a.name < b.name) {
        return -1;
      }
      return 0;
    }
  }
};
</script>

<style lang="scss">
$notRatingColor: lightgray;

// for headers on statistics page not to wrap
.text-start.sortable {
  padding-left: 3px !important;
  padding-right: 3px !important;
}

.games-statistic {
  height: 32px;
  line-height: 32px;

  &.games-statistic-all {
    background-color: yellow;
  }

  &.games-statistic-red-wins {
    background-color: lightcoral;
  }

  &.games-statistic-black-wins {
    color: white;
    background-color: rgb(31, 31, 31);
  }

  &.games-statistic-rating {
    background-color: $notRatingColor;
  }
}

.notRating {
  background-color: $notRatingColor !important;
}

//disable hovered row selection
tr:hover {
  background-color: transparent !important;

  &.notRating {
    background-color: $notRatingColor !important;
  }
}
</style>

