<template>
  <v-data-table
    :headers="headers"
    :items="items"
    :options.sync="options"
    :server-items-length="1"
    disable-pagination
    hide-default-footer
    must-sort
  >
    <template v-slot:item.sherif="{ item }">
      <v-chip
        v-if="displayChip(item, 'sherif')"
        :color="getColor(item, 'sherif')"
      >{{ item.sherifWins }}/{{ item.sherif }}&nbsp;&nbsp;&nbsp;{{ item.sherifWinPercent }}%</v-chip>
      <span
        v-if="!displayChip(item, 'sherif')"
      >{{ item.sherifWins }}/{{ item.sherif }}&nbsp;&nbsp;&nbsp;{{ item.sherifWinPercent }}%</span>
    </template>
    <template v-slot:item.citizen="{ item }">
      <v-chip
        v-if="displayChip(item, 'citizen')"
        :color="getColor(item, 'citizen')"
      >{{ item.citizenWins }}/{{ item.citizen }}&nbsp;&nbsp;&nbsp;{{ item.citizenWinPercent }}%</v-chip>
      <span
        v-if="!displayChip(item, 'citizen')"
      >{{ item.citizenWins }}/{{ item.citizen }}&nbsp;&nbsp;&nbsp;{{ item.citizenWinPercent }}%</span>
    </template>
    <template v-slot:item.don="{ item }">
      <v-chip
        v-if="displayChip(item, 'don')"
        :color="getColor(item, 'don')"
      >{{ item.donWins }}/{{ item.don }}&nbsp;&nbsp;&nbsp;{{ item.donWinPercent }}%</v-chip>
      <span
        v-if="!displayChip(item, 'don')"
      >{{ item.donWins }}/{{ item.don }}&nbsp;&nbsp;&nbsp;{{ item.donWinPercent }}%</span>
    </template>
    <template v-slot:item.maf="{ item }">
      <v-chip
        v-if="displayChip(item, 'maf')"
        :color="getColor(item, 'maf')"
      >{{ item.mafWins }}/{{ item.maf }}&nbsp;&nbsp;&nbsp;{{ item.mafWinPercent }}%</v-chip>
      <span
        v-if="!displayChip(item, 'maf')"
      >{{ item.mafWins }}/{{ item.maf }}&nbsp;&nbsp;&nbsp;{{ item.mafWinPercent }}%</span>
    </template>
  </v-data-table>
</template>

<script>
export default {
  name: "RoleStatistic",
  props: { playerStatisticsForDisplay: Array, players: Array },
  data: () => ({
    items: [],
    options: {},
    headers: [
      { text: "Игрок", value: "name" },
      { text: "Лучший шериф", value: "sherif" },
      { text: "Лучший мирный", value: "citizen" },
      { text: "Лучший дон", value: "don" },
      { text: "Лучший маф", value: "maf" }
    ]
  }),
  watch: {
    options: {
      handler() {
        this.getData().then(data => {
          this.items = data.items;
        });
      },
      deep: true
    }
  },
  methods: {
    displayChip(item, role) {
      var property = role + "WinPercent";
      var highligting = this.getHighligting(item[property], item[role]);
      return highligting != "none";
    },
    getColor(item, role) {
      var property = role + "WinPercent";
      return this.getHighligting(item[property], item[role]);
    },
    getHighligting(percent, total) {
      if (total == 0) {
        return "none";
      }
      if (percent == 100) {
        return "rgb(0, 255, 0)";
      } else if (percent > 60) {
        return "rgba(0, 255, 0, 0.5)";
      } else if (percent == 0) {
        return "rgb(255, 0, 0)";
      }
      return "none";
    },
    getData() {
      return new Promise(resolve => {
        const { sortBy, sortDesc } = this.options;

        let items = this.playerStatisticsForDisplay.slice(0);
        const total = items.length;

        if (
          (sortBy.length === 1 && sortDesc.length === 1) ||
          sortBy.length == 0
        ) {
          let sortByName = "name";
          let sortOrderIsDesc = false;

          if (sortBy.length == 1) {
            sortByName = sortBy[0];
            sortOrderIsDesc = sortDesc[0];
          }

          items = items.sort((a, b) => {
            if (["sherif", "citizen", "don", "maf"].includes(sortByName)) {
              return this.percentSort(a, b, sortByName, sortOrderIsDesc);
            }

            const sortA = a[sortByName];
            const sortB = b[sortByName];

            if (sortOrderIsDesc) {
              if (sortA < sortB) return 1;
              if (sortA > sortB) return -1;
              return 0;
            } else {
              if (sortA < sortB) return -1;
              if (sortA > sortB) return 1;
              return 0;
            }
          });
        }

        resolve({ items, total });
      });
    },
    percentSort(a, b, sortBy, isDesc) {
      var percentKey = sortBy + "WinPercent";
      if (a[percentKey] > b[percentKey]) {
        return isDesc ? -1 : 1;
      }
      if (a[percentKey] < b[percentKey]) {
        return isDesc ? 1 : -1;
      }

      if (a[sortBy] > b[sortBy]) {
        return isDesc ? -1 : 1;
      }
      if (a[sortBy] < b[sortBy]) {
        return isDesc ? 1 : -1;
      }

      return 0;
    }
  }
};
</script>