<template>
  <div class="player-statistic" v-if="selectedPlayerId">
    <div class="select-player-container">
      <div class="player-lable">Игрок:</div>
      <div class="player-selector">
        <model-list-select
          :list="players"
          v-model="selectedPlayerId"
          option-value="id"
          option-text="name"
          placeholder="выберите игрока"
        ></model-list-select>
      </div>
    </div>
    <v-data-table
      :headers="headers"
      :items="items"
      :options.sync="options"
      :server-items-length="1"
      disable-pagination
      hide-default-footer
      must-sort
    >
      <template v-slot:item.bothRed="{ item }">
        <v-chip
          v-if="displayChip(item, 'bothRed')"
          :color="getColor(item, 'bothRed')"
        >{{ item.bothRedWins }}/{{ item.bothRed }}&nbsp;&nbsp;&nbsp;{{ item.bothRedWinPercent }}%</v-chip>
        <span
          v-if="!displayChip(item, 'bothRed')"
        >{{ item.bothRedWins }}/{{ item.bothRed }}&nbsp;&nbsp;&nbsp;{{ item.bothRedWinPercent }}%</span>
      </template>
      <template v-slot:item.bothBlack="{ item }">
        <v-chip
          v-if="displayChip(item, 'bothBlack')"
          :color="getColor(item, 'bothBlack')"
        >{{ item.bothBlackWins }}/{{ item.bothBlack }}&nbsp;&nbsp;&nbsp;{{ item.bothBlackWinPercent }}%</v-chip>
        <span
          v-if="!displayChip(item, 'bothBlack')"
        >{{ item.bothBlackWins }}/{{ item.bothBlack }}&nbsp;&nbsp;&nbsp;{{ item.bothBlackWinPercent }}%</span>
      </template>
      <template v-slot:item.playerRed="{ item }">
        <v-chip
          v-if="displayChip(item, 'playerRed')"
          :color="getColor(item, 'playerRed')"
        >{{ item.playerRedWins }}/{{ item.playerRed }}&nbsp;&nbsp;&nbsp;{{ item.playerRedWinPercent }}%</v-chip>
        <span
          v-if="!displayChip(item, 'playerRed')"
        >{{ item.playerRedWins }}/{{ item.playerRed }}&nbsp;&nbsp;&nbsp;{{ item.playerRedWinPercent }}%</span>
      </template>
      <template v-slot:item.playerBlack="{ item }">
        <v-chip
          v-if="displayChip(item, 'playerBlack')"
          :color="getColor(item, 'playerBlack')"
        >{{ item.playerBlackWins }}/{{ item.playerBlack }}&nbsp;&nbsp;&nbsp;{{ item.playerBlackWinPercent }}%</v-chip>
        <span
          v-if="!displayChip(item, 'playerBlack')"
        >{{ item.playerBlackWins }}/{{ item.playerBlack }}&nbsp;&nbsp;&nbsp;{{ item.playerBlackWinPercent }}%</span>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import { ModelListSelect } from "vue-search-select";

export default {
  name: "PlayerStatistic",
  props: { playerStatisticsForDisplay: Array, players: Array },
  components: {
    ModelListSelect
  },
  data: () => ({
    currentSort: "name",
    currentSortOrder: "asc",
    selectedPlayerId: null,
    selectedPlayerName: "",
    statisticWithOtherPlayers: null,
    items: [],
    options: {},
    headers: [
      { text: "Игрок", value: "name" },
      { text: "Вместе красные", value: "bothRed" },
      { text: "Вместе чёрные", value: "bothBlack" },
      { text: "Разноцвет(игрок красный)", value: "playerRed" },
      { text: "Разноцвет(игрок чёрный)", value: "playerBlack" }
    ]
  }),
  mounted() {
    this.selectedPlayerId = this.players[0].id;
  },
  watch: {
    selectedPlayerId: function(val) {
      this.selectedPlayerName = this.getPlayerName(val);
      this.statisticWithOtherPlayers = this.playerStatisticsForDisplay.find(
        x => x.id === val
      ).withOtherPlayers;
      this.updateTable();
    },
    options: {
      handler() {
        this.updateTable();
      },
      deep: true
    }
  },
  methods: {
    displayChip(item, role) {
      var property = role + "WinPercent";
      var highligting = this.getHighligting(item[property], item[role]);
      return highligting != "none";
    },
    getColor(item, role) {
      var property = role + "WinPercent";
      return this.getHighligting(item[property], item[role]);
    },
    getHighligting(percent, total) {
      if (total == 0) {
        return "none";
      }

      if (percent == 100) {
        return "rgb(0, 255, 0)";
      } else if (percent > 60) {
        return "rgba(0, 255, 0, 0.5)";
      } else if (percent == 0) {
        return "rgb(255, 0, 0)";
      }
      return "none";
    },
    updateTable() {
      this.getData().then(data => {
        this.items = data.items;
      });
    },
    getData() {
      return new Promise(resolve => {
        const { sortBy, sortDesc } = this.options;

        let items = this.statisticWithOtherPlayers.slice(0);
        const total = items.length;

        if (
          sortBy == undefined ||
          (sortBy.length === 1 && sortDesc.length === 1) ||
          sortBy.length === 0
        ) {
          let sortByName = "name";
          let sortOrderIsDesc = false;

          if (sortBy != undefined && sortBy.length == 1) {
            sortByName = sortBy[0];
            sortOrderIsDesc = sortDesc[0];
          }
          items = items.sort((a, b) => {
            if (
              ["bothRed", "bothBlack", "playerRed", "playerBlack"].includes(
                sortByName
              )
            ) {
              return this.percentSort(a, b, sortByName, sortOrderIsDesc);
            }

            const sortA = a[sortByName];
            const sortB = b[sortByName];

            if (sortOrderIsDesc) {
              if (sortA < sortB) return 1;
              if (sortA > sortB) return -1;
              return 0;
            } else {
              if (sortA < sortB) return -1;
              if (sortA > sortB) return 1;
              return 0;
            }
          });
        }

        resolve({ items, total });
      });
    },
    percentSort(a, b, sortBy, isDesc) {
      var percentKey = sortBy + "WinPercent";
      if (a[percentKey] > b[percentKey]) {
        return isDesc ? -1 : 1;
      }
      if (a[percentKey] < b[percentKey]) {
        return isDesc ? 1 : -1;
      }

      if (a[sortBy] > b[sortBy]) {
        return isDesc ? -1 : 1;
      }
      if (a[sortBy] < b[sortBy]) {
        return isDesc ? 1 : -1;
      }

      return 0;
    },
    getPlayerName(id) {
      return this.players.find(x => x.id === id).name;
    }
  }
};
</script>

<style lang="scss">
$col-width: 200px;
.md-tab {
  padding-left: 0px !important;
  padding-right: 0px !important;

  .player-statistic {
    .select-player-container {
      text-align: left;
      padding-left: 10px;
      margin-bottom: 10px;

      .player-lable {
        display: inline-block;
        width: 50px;
      }
      .player-selector {
        display: inline-block;
        width: 200px;
      }
    }
  }
}
</style>